"use client";

import { useParams, useRouter } from "next/navigation";
import { useEffect, useState } from "react";
import { FaArrowLeft, FaEdit, FaTrash, FaFilePdf } from "react-icons/fa";
import EmployeeModal, { Employee } from "@/components/EmployeeModal";

export default function EmployeeDetailPage() {
  const { id } = useParams();
  const router = useRouter();
  const [employee, setEmployee] = useState<Employee | null>(null);

  useEffect(() => {
    const fetchEmployee = async () => {
      try {
        const res = await fetch(`/api/employees/${id}`);
        if (res.ok) {
          const data = await res.json();
          setEmployee(data);
        }
      } catch (err) {
        console.error("Failed to fetch employee:", err);
      }
    };
    fetchEmployee();
  }, [id]);

  if (!employee) return <div className="container mt-5">Loading...</div>;

  return (
    <div className="container mt-5">
      {/* Header with Back + Title + Actions */}
      <div className="d-flex justify-content-between align-items-center mb-4">
        <button className="btn btn-outline-secondary" onClick={() => router.push("/employees")}>
          <FaArrowLeft /> Back
        </button>

        <h2 className="m-0">{employee.firstName} {employee.lastName}</h2>

        <div>
          <EmployeeModal
            mode="edit"
            employee={employee}
            onSubmit={(emp) => console.log("Edit employee", emp)}
            buttonText={<><FaEdit /> Edit</>}
            className="btn btn-success btn-sm me-2"
          />
          <EmployeeModal
            mode="delete"
            employee={employee}
            onSubmit={(emp) => console.log("Delete employee", emp)}
            buttonText={<><FaTrash /> Delete</>}
            className="btn btn-danger btn-sm me-2"
          />
          <button className="btn btn-outline-danger btn-sm">
            <FaFilePdf /> PDF
          </button>
        </div>
      </div>

      {/* Employee Details */}
      <div className="card p-3 mb-4">
        <h5>Employee Details</h5>
        <p><strong>Rate:</strong> {employee.rate}</p>
        <p><strong>Work Days:</strong> {employee.workDays}</p>
      </div>

      {/* Projects Section */}
      <div className="card p-3 mb-4">
        <h5>Projects</h5>
        <p>List of projects will go here...</p>
      </div>

      {/* Invoices Section */}
      <div className="card p-3">
        <h5>Invoices</h5>
        <p>List of invoices will go here...</p>
      </div>
    </div>
  );
}
